.social {
  font-size: 12px;
  color: $light;
  text-transform: uppercase;

  &__item {
    @include lg {
      display: inline-block; } }

  &__link {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }

  &__title {
    @include xs {
      display: none; }

    &--mob {
      display: none;
      @include xs {
        display: inline; } } } }
