.nav {
  text-transform: uppercase;
  color: $dark;

  ul {
    display: flex;
    align-items: center;

    @include md {
      display: block; } }

  a {
    display: flex;
    align-items: center;
    justify-content: center;

    height: $header-height;
    border-right: 1px solid  $c-border;

    padding-left: rem(20);
    padding-right: rem(20);

    @include tr(color .3s);
    @include hover {
      color: $accent; }

    @include lg {
      height: $header-height-lg; }

    &:first-child {
      border-left: 1px solid $c-border; }

    .icon {
      margin-right: .3em;
      font-size: 1.5em; } }

  &--footer {
    color: inherit;
    ul {
      display: block !important; }
    li {
      display: inline-block;
      @include notlast {
        margin-right: rem(25);
        @include xs {
          margin-right: 0;
          margin-bottom: rem(15); } }
      @include xs {
        display: block; } }
    a {
      display: inline;
      hight: auto !important;
      border: 0 !important;
      padding: 0 !important;
      @include hover {
        color: inherit;
        text-decoration: underline; } } } }
