.header {
  background-color: $white;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  border: 1px solid  $c-border;
  @include md {
    border: 0;
    border-bottom: 1px solid  $c-border;
    box-shadow: 0 0 10px rgba($black, .25); }

  &__inner {
    display: flex;
    align-items: center;

    @include md {
      height: $header-height-lg; } }

  &__logo {
    margin-right: rem(25);
    position: relative;
    z-index: 10;
    @include lg {
      flex: 0 0 150px; } }

  &__nav {
    @include md {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      z-index: 5;
      background-color: $white;
      transform: translate(0, calc(-100% - #{$header-height-lg}));
      @include ready {
        @include tr(transform .5s $easeInQuad); }
      &--open {
        transform: translate(0, 0); } } }

  &__burger {
    display: block;
    margin-left: auto;
    position: relative;
    z-index: 10;
    .icon {
      font-size: rem(30); }
    @include mdmin {
      display: none; } } }
