small {
  font-size: inherit; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.title {
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1.2;

  &--h2 {
    font-size: em(48);
    @include lg {
      font-size: em(44); }
    @include md {
      font-size: em(40); }
    @include sm {
      font-size: em(36); }
    @include xs {
      font-size: em(32); } }
  &--h3 {
    font-size: em(22); }

  &--ttn {
    text-transform: none; } }
